html {
  min-height: 100vh;
  box-sizing: border-box; }

body {
  padding: 0;
  margin: 0;
  font-family: "IBM Plex Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  position: relative;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  min-height: 100vh; }

*,
*::after,
*::before {
  box-sizing: inherit; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.hidden {
  display: none; }

.hidden-desktop {
  display: none; }
  @media (max-width: 767px) {
    .hidden-desktop {
      display: block; } }

.hidden-mobile {
  display: block; }
  @media (max-width: 767px) {
    .hidden-mobile {
      display: none; } }

button {
  cursor: pointer; }

.header {
  display: flex;
  padding-top: 60px;
  padding-bottom: 20px;
  font-family: "IBM Plex Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  color: #ffffff;
  background-color: #000000; }
  .header__container {
    max-width: 1150px;
    margin: 0 auto;
    padding: 0 15px;
    display: flex;
    flex-grow: 1; }
  .header__logo-link {
    margin-right: auto; }
  .header__nav-list {
    display: flex;
    margin: 0;
    padding: 0;
    list-style-type: none; }
  .header__nav-item {
    margin-left: 29px; }
  .header__nav-link {
    position: relative;
    display: block;
    padding-top: 15px;
    padding-bottom: 36px;
    font: inherit;
    text-decoration: none;
    color: inherit; }
    .header__nav-link::before {
      content: "";
      display: block;
      position: absolute;
      bottom: 0;
      height: 4px;
      width: 100%;
      background-color: transparent; }
    .header__nav-link:hover:not(.header__nav-link--active)::before {
      background-color: rgba(255, 255, 255, 0.3); }
    .header__nav-link--active::before {
      background-color: #ffffff; }

.approach {
  padding-top: 75px;
  padding-bottom: 102px;
  overflow: hidden;
  background-color: #000000; }
  .approach__container {
    max-width: 1150px;
    margin: 0 auto;
    padding: 0 15px;
    display: flex; }
  .approach__content {
    flex-shrink: 0;
    width: 592px; }
  .approach__img-block {
    flex-shrink: 0;
    padding-top: 5px; }
  .approach__img {
    margin-left: 80px; }
  .approach__title {
    margin: 0;
    padding: 0;
    padding-bottom: 45px;
    font-weight: 600;
    font-size: 90px;
    line-height: 117px; }
  .approach__text-wrap {
    padding-bottom: 44px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.4); }
  .approach__subtitle {
    margin: 0;
    padding: 0;
    font-weight: 600;
    font-size: 20px;
    line-height: 30px; }
  .approach__text {
    margin: 0;
    padding: 0;
    padding-top: 42px;
    font-size: 18px;
    line-height: 1.6; }
  .approach__subtext {
    padding-top: 40px; }
  .approach__subtext p {
    margin: 0;
    padding: 0;
    padding-top: 36px;
    font-size: 18px;
    line-height: 1.6; }
